// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

// npm 
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import 'bootstrap'; //bootstrap
import Loading from 'vue-loading-overlay'; //
import 'vue-loading-overlay/dist/vue-loading.css'; //CSS
import VeeValidate from 'vee-validate'; //
import zhTW from 'vee-validate/dist/locale/zh_TW'; //
import VueI18n from 'vue-i18n';


// 
import router from './router';
import App from './App';
import './bus';
import currencuFilter from './filters/currency';
import dateFilter from './filters/date';

//
Vue.config.productionTip = false;
Vue.use(VueAxios, axios);
Vue.component('Loading',Loading); //
Vue.filter('currency',currencuFilter ); //
Vue.filter('date', dateFilter); //
Vue.use(VueI18n);


axios.defaults.withCredentials = true; //Cookie

//VeeValidate
const i18n = new VueI18n({
  locale: 'zhTW'
});
Vue.use(VeeValidate, {
  i18n,
  dictionary: {
    zhTW
  }
});


/* eslint-disable no-new */
new Vue({
  i18n,
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
});


router.beforeEach((to, from, next) => {
  //
  if(to.meta.requiresAuth){
    //tometa.requiresAuthture
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)zplusToken\s*\=\s*([^;]*).*$)|^.*$/,
      "$1",
    );
    console.log(token)
    axios.defaults.headers.common['Authorization'] = token;
    const api =`${process.env.APIPATH}/api/user/check`;
    axios.post(api).then((response) => {
    console.log(response.data);
    if(response.data.success){
      next();  // from(Login)next(productsLogin)
    }else{
      next({
        path:'/login',  // Login
      })
    }
    });
  }else{
    next();  //meta.requiresAuth
  }




})



